$(".januari").each(function () {
  var j = 1;
  for (var i = 0; i < 37; i++) {
    if (i > 2 && i < 34) {
      $(this).append(`<div class='grid-selectable'>  
      <div class="grid-selectable-header"><span>${j}</span>
      </div>
      <div class="grid-selectable-body">
      

      <div class="year-content">
      <div class="price-info">
      <span class="special-span">â‚¬</span>
      <span  class="special-span">1430</span>
      <span class="special-span">-</span>
      <span  class="special-span">2300</span>
      </div>
      <div class="employee-info">
      <i class="fas fa-user personnel-info-icon special-span"></i>
      <span class="special-span">12</span>
      <span class="special-span">-</span>
      <span  class="special-span">47</span>
      </div>
      <div class="hours-info">
      <i class="fas fa-clock personnel-info-icon special-span"></i>
    <span  class="special-span">1241</span>
    <span class="special-span">-</span>
    <span  class="special-span">1326</span>
      </div>
      
      </div>
      </div>`);
      j++;
    } else {
      $(this).append("<div class='grid-not-selectable'></div>");
    }
  }
});

